@import url(https://fonts.googleapis.com/css?family=Roboto:500);
:root{
  --white-dark: #d8e3e7;
  --white:#fefefe;
  --google-blue: #4285f4;
  --blue-light:#51c4d3;
  --blue:#126e82;
  --blue-dark:#132c33;
  --blue-shadow:rgb(97,217,250,.2);
  --react-blue:#20cfff;
  --react-blue-light:#61dafb;
  --react-dark:#282c34;
  --button-active-blue: #1669F2;
}

*{
  max-width: 100vw;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  padding:0;
  margin:0;
}
body {
  padding:0;
  margin:0;
}
.App {
  text-align: center;
}

.App-logo {
  width: 40vw;
  pointer-events: none;
}

.App-header-home .App-logo {
  width: 40vw;
  position: absolute;
  right: 6vw;
  top: 25vh;
  pointer-events: none;
}

a {
  color:var(--react-blue);
  text-decoration: none;
}
ul{
  margin-left: 6vw;
}
li {
  list-style-type: none;
  /* display: inline-block; */
  float: left;
  padding:3vh 0;
  margin:0 10px;
}

.user-profile{
  float:right;
  align-items: center;
  height: 3vh;
  padding: 3vh 6vw;
}

.user-profile p {
  display: inline-block;
}

.user-profile-picture {
  border-radius: 50%;
  height: 10vh;
  margin: 5vh;
}

.App-nav{
  background-color: transparent;
  color:var(--react-blue);
  position: absolute;
  display:inline-block;
  top:0;
  padding:0;
  left: 0;
  min-height: 3vh;
  width: 100vw;
  font-weight: 700;
}

.logout-button {
  background-color: transparent;
  margin:0 10px;
}

.opening {
  text-align: left;
  display: inline-block;
  box-shadow:1vw 2vh 50px -2px var(--blue-shadow);
  position: static;
  color: var(--react-blue);
  text-shadow: 0 0 20px var(--blue-shadow);
  border-radius: 6vh;
  margin: 10vh 2vw;
  padding: 15vh 40vw 10vh 10vw;
  max-width: 43vw; 
}
.opening p {
  font-size: .7em;
  margin: 5vh 0;
}

.App-header {
  padding-top:2vh;
  background-color: var(--white);
  min-height: 98vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: var(--react-blue);
}
.App-header-home {
  padding-top:2vh;
  background-color: var(--white);
  min-height: 98vh;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  font-size: calc(10px + 2vmin);
  color: var(--react-blue);
}

.App-link {
  color: #61dafb;
}

.Main-logo {
  position:absolute;
  max-width: 30px;
  top: 2.5vh;
  left: 2vw;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

@media (max-width:960px){
  .App-logo{
    display: none;
  }
  .opening{
    max-width: 70vw;
    padding: 15vh 10vw 10vh 10vw;
  }
}
@media (max-width:768px){
  .Main-logo{
    display: none;
  }
}
@media (max-width:425px){
  .App-nav img{
    display: none;
  }
}
