.google-btn {
  width: 184px;
  height: 42px;
  padding:1vw;
  background-color: var(--react-blue);
  border-radius: 1vw;
  box-shadow: 0 3px 4px 0 rgba(0,0,0,.25);
  -webkit-transform: scale(.75);
          transform: scale(.75);
}
  .google-icon-wrapper {
    position: absolute;
    margin-top: 1px;
    margin-left: 1px;
    width: 40px;
    height: 40px;
    border-radius: 2vw;
    background-color: var(--white);
  }
  .google-icon {
    position: absolute;
    margin-top: 11px;
    margin-left: -9px;
    width: 18px;
    height: 18px;
  }
  .btn-text {
    float: right;
    margin: 11px 11px 0 0;
    color: var(--white);
    font-size: 14px;
    letter-spacing: 0.2px;
    font-family: "Roboto";
  }btn-text:hover{cursor:grab}
  .google-btn:hover {
    box-shadow: 0 0 6px var(--google-blue);
    cursor:auto;
  }
  .google-btn:active {
    background: var(--button-active-blue);
  }

.card {
  background-color:var(--white);
  padding: 8vh 4vw;
  border-radius: 2vw;
  box-shadow:1vw 0vh 50px -2px rgb(97,217,250,.3);
}

.register-input{
    display:block;
    border: transparent;
    border-bottom: 2px solid var(--react-blue);
    background: var(--white);
    color: var(--react-blue);
    padding: 10px 5px;
    margin: 5px 0;
    width: 100%;
}
.register-input::-webkit-input-placeholder {
    color: var(--react-blue);
}
.register-input:-ms-input-placeholder {
    color: var(--react-blue);
}
.register-input::placeholder {
    color: var(--react-blue);
}

.register-link {
    font-size:.8rem;
    border: none;
    font-weight: 700;
    text-shadow: 0 0 1px var(--react-blue);
    background-color:transparent;
    color: var(--react-blue);
    margin: 0 10px;
}
.register-link:hover {
  color:var(--react-dark);
  transition:.2s;
}
